* {
  margin: 0;
  padding: 0;
}

body {
  background: url(i/sportBg.jpg) no-repeat;
  background-size: cover;
  background-repeat: no-repeat;
  font-family: Arial, Helvetica, sans-serif;
}

#root {
  height: 100vh;
  width: 100%;
}

#root::before {
  background-color: rgba(255, 0, 0, 0.87);
  mix-blend-mode: multiply;
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.extra {
  height: 100vh;
  width: 100%;
  background: linear-gradient(0deg, rgba(84, 84, 84, 0) 0%, rgb(0, 0, 0) 100%);
}

.brand {
  mix-blend-mode: normal;
}
